.Pagination-container{display:flex;flex-direction:column;justify-content:center;align-items:center;font-size:14px;margin-bottom:100px;margin-top:30px;font-family:"Mango"}.Pagination-container a{height:25px;line-height:25px;text-decoration:none;color:#888}.Pagination-container ul{list-style:none}.Pagination-container li{float:left;height:25px;display:flex;align-items:center;justify-content:center}.Pagination-container .page{margin:0 5px;cursor:pointer;width:25px;border-radius:30px;border:1px solid rgba(0,0,0,0);text-align:center}.Pagination-container .page:hover{border:1px solid #aaa}.Pagination-container .move{position:relative;cursor:pointer;margin:0 10px}.Pagination-container .move:last-child::after,.Pagination-container .move:first-child::before{position:absolute;font-size:20px;padding:0 7px 0px}.Pagination-container .move:first-child{text-align:right}.Pagination-container .move:first-child::before{content:"«";left:-20px;top:-5px}.Pagination-container .move:last-child::after{content:"»";right:-10px;top:-5px}.Pagination-container .move a{width:50px;display:block;z-index:10}.Pagination-container .move a:hover{text-decoration:underline}.Pagination-container .invisible{visibility:hidden}.Pagination-container .active{font-weight:bold;background:#2f5d62;color:#fff}.Pagination-container .inputBox{margin-top:15px;display:flex;align-items:center}.Pagination-container .inputBox input{text-align:center;color:#222;width:200px;border:none;border-bottom:1px solid #aaa;padding-bottom:5px;background:none;z-index:5;font-family:"Mango"}.Pagination-container .inputBox input:focus{outline:none}.Pagination-container .inputBox button{cursor:pointer;margin-left:10px;width:50px;height:30px;padding:0;border:none;font-weight:bold;background:#000;color:#fff;transition:all .3s ease;position:relative;display:inline-block;border-radius:5px;font-family:"Mango"}.Pagination-container .inputBox button:hover{background:rgba(0,0,0,0);color:#000;box-shadow:-7px -7px 20px 0px rgba(255,255,255,.6),-4px -4px 5px 0px rgba(255,255,255,.6),7px 7px 20px 0px rgba(0,0,0,.1333333333),4px 4px 5px 0px rgba(0,0,0,.0666666667)}.Pagination-container .inputBox button::before,.Pagination-container .inputBox button::after{content:"";position:absolute;top:0;right:0;height:2px;width:0;background:#000;transition:.4s ease all}.Pagination-container .inputBox button::after{right:inherit;top:inherit;left:0;bottom:0}.Pagination-container .inputBox button:hover::before,.Pagination-container .inputBox button:hover::after{width:100%;transition:.8s ease all}@media(max-width: 768px){.Pagination-container ul{width:250px}.Pagination-container li{margin-bottom:15px}}