@import"https://cdnjs.cloudflare.com/ajax/libs/weather-icons/1.2/css/weather-icons.min.css";.Weather-container{font-size:18px;font-family:"Mango"}.Weather-container h1,.Weather-container h2,.Weather-container h3,.Weather-container h4,.Weather-container h5{font-weight:normal;margin:0;padding:0}.Weather-container .widget{height:300px;width:450px;margin:0 auto;position:relative}.Weather-container .weatherIcon{background-color:#f0f8fa;height:70%;width:100%;position:absolute;left:0;top:0;border-top-left-radius:10px;border-top-right-radius:10px}.Weather-container .weatherIcon::before{display:block;content:"";color:#818b8d;font-family:weathericons;font-size:110px;position:absolute;left:50%;top:58%;transform:translate(-50%, -50%)}.Weather-container .weatherData{background-color:#2e3336;height:30%;width:100%;position:absolute;left:0;bottom:0;border-bottom-left-radius:10px;border-bottom-right-radius:10px}.Weather-container h1{color:#c5cdcf;font-family:inherit;font-weight:200;font-size:2em !important;line-height:2.9375em;position:absolute;left:6%;top:50%;transform:translate(0, -50%)}.Weather-container h2{color:#f0f8fa;font-family:inherit;font-weight:200;font-size:1.1875em;line-height:1.1875em;position:absolute;top:35%;left:27%}.Weather-container h4{color:#fff;font-family:inherit;font-weight:700;text-transform:uppercase;font-size:.6875em;line-height:.6875em;position:absolute;top:25%;left:50%;transform:translate(-50%, 0)}.Weather-container h5{color:#fff;font-family:inherit;font-weight:500;font-size:1.8125em;line-height:28px;position:absolute;bottom:24%;left:50%;transform:translate(-50%, 0)}.Weather-container .date{background-color:#4dbde2;height:30%;width:22%;position:absolute;right:0;bottom:0;border-bottom-right-radius:10px}@media(max-width: 768px){.Weather-container .widget{width:100%;height:200px}.Weather-container .weatherIcon::before{font-size:80px}.Weather-container h1{font-size:1.5em}.Weather-container h2{font-size:.8em}.Weather-container h4{font-size:.5em}.Weather-container h5{font-size:1.1em;bottom:15%}}